/* 
====== Font Theme ====== 
<h1>  heading     (250%)   
<h2>  subheading  (200%)   
<p>   text        (100%)   
<p>   subtext     (75%, 80%) 
*/

html,
* {
  margin: 0;
  padding: 0;
  font-size: 100%;
  /* best practice for accessibility 100% == 16px (most browsers_ */
  font-family: "Montserrat", sans-serif;
}

.flex-box {
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

/* edit button that produces the popup form */
.flex-box #edit-button {
  width: 1.2rem;
  margin: -10px 0 0 1rem;
}

.wrapper-col-2 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column-reverse;
          flex-direction: column-reverse;
  width: 100%;
  min-height: 500px;
  justify-items: center;
  -webkit-align-items: center;
          align-items: center;
}

.wrapper-row-3 {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 60vh;
}

.col {
  padding: 0;
  margin: 0;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.fill-page {
  position: absolute;
  top: 0;
}

.box {
  height: 325px;
  width: 425px;
}

.right-auth h1,
.box h1 {
  font-family: "Fredoka One", cursive;
  text-align: left;
  font-size: 200%;
}

.box p {
  margin: 5% 0 5% 0;
  text-align: left;
  font-size: 100%;
}

/* @Override React Bootstrap Button colors */
.btn {
  width: 100%;
  font-weight: 600 !important;
}

.btn-fill {
  width: 100%;
  height: 100% !important;
  font-weight: 600 !important;
}

/*  
====== Button Theme ======  
flat: #65C2AC
darK: #2B2B2B
*/

.btn-flat {
  background-color: #65c2ac !important;
}

.btn-flat:hover {
  background-color: #2b2b2b !important;
  color: #fff !important;
}

.btn-dark {
  background-color: rgb(43, 43, 43, 1) !important;
  color: white;
}

.btn-dark:hover {
  background-color: rgb(60, 60, 60) !important;
}

.subtext {
  font-size: 80%;
