  margin: 0 auto 0 auto;
}

/* HIDE RADIO */
[type="radio"] {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

/* IMAGE STYLES */
[type="radio"] + #student-radio,
[type="radio"] + #professor-radio,
[type="radio"] + #company-radio {
  cursor: pointer;
}

#student-radio:hover,
[type="radio"]:checked + #student-radio {
  fill: #65c2ac !important;
}

#professor-radio:hover,
[type="radio"]:checked + #professor-radio {
  fill: #efe271 !important;
}

#company-radio:hover,
[type="radio"]:checked + #company-radio {
  fill: #61adcf !important;
}

/* CHECKED STYLES */
[type="radio"]:checked + #student-radio,
[type="radio"]:checked + #professor-radio,
[type="radio"]:checked + #company-radio {
  padding: 0;
  box-shadow: -4px 4px 3px 2px rgba(80, 80, 80, 0.2);
  border-radius: 10px;
  border-style: solid;
  border-radius: 15% !important;
}

