/* ======= GRID LAYOUT ======= */
.grid-container {
  display: grid;
  grid-template-columns: 0.3fr 1.5fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  grid-template-areas:
    "header header"
    "filter results";
}

/* Search bar */
.header {
  margin-top: 1rem;
  margin-bottom: 1rem;
  grid-area: header;
}

/* Search filters */
.filter {
  grid-area: filter;
  max-width: 250px;
  min-width: 220px;
  overflow-y: scroll;
  overflow: -moz-scrollbars-vertical;
  border-right: solid;
  border-right-color: #eee !important;
}

/* Results of the search */
.results {
  grid-area: results;
  background-color: rgba(0, 0, 0, 0.03);
  display: grid;
  grid-gap: 1rem;
  gap: 1rem;
  grid-template-columns: repeat(auto-fill, 340px) !important;
  grid-template-rows: repeat(auto-fill, 185px) !important;
  padding-left: 1rem;
  min-height: 100vh;
}

/* ======= SEARCH BAR STYLING ======= */

#search-students .mb-3 {
  width: 70%;
  margin-right: 20px;
}

/* Search Bar */
.search-bar,
.search-prepend {
  background-color: #eee;
  border-style: none;
  height: 45px;
}

.search-prepend {
  font-weight: 600;
}

.search-bar:focus {
  background-color: #eee;
  border-style: none;
}

/* Search button for students & job search */
.search-btn {
  width: 30%;
  background-color: #efe271;
  color: #2b2b2b;
  border-style: none;
  height: 45px;
}

.search-btn:hover,
.search-btn:focus,
.flex-box Button {
  background-color: #2b2b2b !important;
  color: #fff !important;
  border-style: none;
}

/* ======= Filter Styling ======= */

.filter,
.results {
  border-top: solid;
  border-color: #eee;
  border-width: thin;
  padding-top: 1rem;
}

/* resizes the filter cards (overwrites bootstrap styling)  */
.card-header,
.card-body {
  font-size: 85%;
  border-bottom: none;
}

/* Cards (overwrites bootstrap styling) */
.card {
  border-style: none;
}

/* Resizes placeholders */
.filter ::-webkit-input-placeholder {
  font-size: 75%;
}
.filter :-ms-input-placeholder {
